html, body {
  font-family: 'Open Sans', sans-serif;
}

.container {
  text-align: center;
}

.grid {
  display: grid;
  grid-auto-columns: -webkit-max-content;
  grid-auto-columns: max-content;
  grid-template-columns: repeat(auto-fit, minmax(50px, 1fr));
}

.grid > div {
  text-align: center;
  line-height: 20px;
  padding: 10px;
  font-size: 30px;
}

.container {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.filter {
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-bottom: 20px;
  -ms-flex-direction: column;
      flex-direction: column;
}

@media (min-width: 576px) {
  .filter {
    -ms-flex-direction: row;
        flex-direction: row;
  }
}

.filter select {
  font-size: 20px;
}

.filter p {
  padding-right: 10px;
  padding-left: 10px;
  margin: 0;
}



